

















































































































































































import { Component, Mixins, Prop, Watch } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import DistrictMixin from '@/mixins/commonMixin'
import { namespace } from 'vuex-class'
import LocationRepository from '@/services/repositories/location'
import getRepository from '@/services'
import ContractRepository from '@/services/repositories/contract'
import EmployeeRepository from '@/services/repositories/employee'
import { debounce, trim } from 'lodash'
import ParamRepository from '@/services/repositories/params'
import DealRepository from '@/services/repositories/deal'

const beDeal = namespace('beDeal')
@Component({ components: {} })
export default class PopupFilterDeal extends Mixins(PopupMixin, DistrictMixin) {
  @Prop({ required: true, type: Object }) filters!: Record<string, any>
  @Prop() dataProp?: Record<string, any>
  groupFilter: Record<string, any> = {
    // dataChange: {}
  }
  @beDeal.State('filters') filterSot!: Record<string, any>
  @beDeal.Getter('getDealFilter') getDealFilter!: (dealName: string) => {
    report: string
    filters: Record<string, any>
    data: Record<string, any>
  }
  @beDeal.Action('setDealFilter') setDealFilter!: (data: { dealName: string; filters: Record<string, any>; data: Record<string, any> }) => void
  apiLocation!: LocationRepository
  apiContract!: ContractRepository
  apiEmployee!: EmployeeRepository
  apiParam!: ParamRepository
  apiDeal!: DealRepository
  value1 = ''
  isOpen = false
  formData: any = {}
  rules = { dealValue: [{ validator: this.validate, trigger: 'blur' }] }
  dataSelect: Record<string, any> = {
    areas: [],
    branches: [],
    offices: [],
    members: [],
    products: []
  }
  listStatus = [
    {
      id: 5,
      label: 'Tất cả',
      value: ''
    },
    {
      id: 1,
      label: 'Đang làm việc',
      value: '1'
    },
    {
      id: 2,
      label: 'Thành công',
      value: '2'
    },
    {
      id: 3,
      label: 'Thất bại',
      value: '3'
    },
    {
      id: 4,
      label: 'Đang làm việc & Thành công',
      value: '0'
    }
  ]
  assignmentStatus = [
    { id: 1, label: 'Đã gán NV phụ trách', value: 1 },
    { id: 2, label: 'Chưa gán NV phụ trách', value: 0 }
  ]
  listPipeline: Record<string, any>[] = []
  listStagePipeline = []
  dataSelectMulti = {}
  // dataChange: any = {}
  disable = {
    area: false,
    branch: false,
    office: false,
    member: false,
    product: false
  }

  query = {
    pipeline: {
      page: 1,
      limit: 10
    },
    areas: {
      i: 0,
      ps: 20,
      search: ''
    },
    branches: {
      i: 0,
      ps: 20,
      search: ''
    },
    offices: {
      i: 0,
      ps: 20,
      search: ''
    },
    products: {
      i: 0,
      ps: 20,
      search: ''
    },
    members: { i: 0, ps: 20, limit: '', page: 1, search: '', statusAccount: 0, groupCode: 'INSURANCE', sort: 1 }
  }
  @Watch('groupFilter.pipelineId')
  getStages(id: number): void {
    // this.groupFilter.currentStageId = ''
    // console.log(this.groupFilter.currentStageId)
    if (id) {
      this.apiDeal.getDataStagesPipelines(id).then(res => (this.listStagePipeline = res.data))
    } else {
      this.listStagePipeline = []
    }
  }
  get toWonDate(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    return {
      disabledDate(time: Date) {
        return self.disabledDateToWonDate(time)
      },
      onPick(time: Date) {
        return time
      }
    }
  }
  get toCloseDate(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    return {
      disabledDate(time: Date) {
        return self.disabledDateToCloseDate(time)
      }
    }
  }
  get toCreatedAt(): any {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    return {
      disabledDate(time: Date) {
        return self.disabledDateToCreatedAt(time)
      }
    }
  }
  handleChangePipeline(): void {
    delete this.groupFilter.currentStageId
  }
  disabledDateToCreatedAt(time: Date): any {
    if (this.groupFilter.fromCreatedAt) return time.getTime() < this.convertDateToTimeStamp(this.groupFilter.fromCreatedAt)
  }
  disabledDateToCloseDate(time: Date): any {
    if (this.groupFilter.fromCloseDate) return time.getTime() < this.convertDateToTimeStamp(this.groupFilter.fromCloseDate)
  }
  disabledDateToWonDate(time: Date): any {
    if (this.groupFilter.fromWonDate) return time.getTime() < this.convertDateToTimeStamp(this.groupFilter.fromWonDate)
  }
  handleChangeNumber(field: string): void {
    if (isNaN(this.groupFilter[field])) {
      this.groupFilter[field] = this.groupFilter[field].replaceAll(',', '')
    }
    if (this.groupFilter[field]) {
      this.groupFilter[field] = this.$options.filters?.formatNumber(+this.groupFilter[field])
    }
  }

  validate(rule: Record<string, any>, value: string, callback: unknown): void {
    const _callback: any = callback
    if (
      rule.field == 'dealValue' &&
      this.groupFilter.toDealValue &&
      this.groupFilter.fromDealValue &&
      this.convertDecimalToNumber(this.groupFilter.toDealValue) < this.convertDecimalToNumber(this.groupFilter.fromDealValue)
    ) {
      _callback(new Error('Giá trị "từ" phải nhỏ hơn giá trị "đến"'))
    } else {
      _callback()
    }
  }

  convertDateToTimeStamp(date: string): number {
    const formatDate = date.split('-')
    const newDate = new Date(+formatDate[0], +formatDate[1] - 1, +formatDate[2])
    return newDate.getTime()
  }
  convertDecimalToNumber(number: string): number {
    return Number(number.replaceAll(',', ''))
  }
  loadMore(type: string): void {
    switch (type) {
      case 'branches':
        this.query.branches.i += this.query.branches.ps
        this.apiLocation.list('branch', this.query.branches).then(res => {
          const listFilter = [...this.dataSelect.branches, ...res]
          this.dataSelect.branches = [...new Map(listFilter.map(item => [item['id'], item])).values()]
        })
        break
      case 'offices':
        this.query.offices.i += this.query.offices.ps
        this.apiLocation.list('office', this.query.offices).then(res => {
          const listFilter = [...this.dataSelect.offices, ...res]
          this.dataSelect.offices = [...new Map(listFilter.map(item => [item['id'], item])).values()]
        })
        break
      case 'members':
        this.query.members.i += this.query.members.ps
        this.apiEmployee.getEmployeeList(this.query.members).then(res => {
          const listFilter = [...this.dataSelect.members, ...res.data]
          this.dataSelect.members = [...new Map(listFilter.map(item => [item['id'], item])).values()]
        })
        break
      // case 'products':
      //   this.query.products.i += this.query.products.ps
      //   this.apiContract.getProductList(this.query.products).then(res => {
      //     const listFilter = [...this.dataSelect.products, ...res.data.content]
      //     this.dataSelect.products = [...new Map(listFilter.map(item => [item['id'], item])).values()]
      //   })
      //   break
      case 'pipeline':
        this.query.pipeline.limit += 10
        this.apiDeal.getDataSalesPipelines(this.query.pipeline).then(res => {
          const listFilter = [...this.listPipeline, ...res.data.content]
          this.listPipeline = [...new Map(listFilter.map(item => [item['id'], item])).values()]
        })
        break
    }
  }
  remoteMethod(type: string, query: string): void {
    const a = debounce(this.handleSearch, 500)
    a(type, query)
  }
  async handleSearch(type: string, search: string): Promise<any> {
    const param = { i: 0, ps: 20, search: trim(search) }
    switch (type) {
      case 'areas':
        this.query.areas = param
        await this.apiLocation.list('area', this.query.areas).then(res => {
          this.dataSelect.areas = res
        })
        break
      case 'branches':
        this.query.branches = param
        await this.apiLocation.list('branch', this.query.branches).then(res => {
          this.dataSelect.branches = res
        })
        break
      case 'offices':
        this.query.offices = param
        await this.apiLocation.list('office', this.query.offices).then(res => {
          this.dataSelect.offices = res
        })
        break
      case 'members':
        this.query.members = { ...this.query.members, ...param }
        await this.apiEmployee.getEmployeeList(this.query.members).then(res => {
          this.dataSelect.members = res.data
        })
        break
      // case 'products':
      //   this.query.products = param
      //   await this.apiContract.getProductList(this.query.products).then(res => {
      //     this.dataSelect.products = res.data.content
      //   })
      // break
      default:
        await Promise.all([
          this.apiLocation.list('area', this.query.areas).then(res => {
            this.dataSelect.areas = res
          }),
          this.apiLocation.list('branch', this.query.branches).then(res => {
            this.dataSelect.branches = res
          }),
          this.apiLocation.list('office', this.query.offices).then(res => {
            this.dataSelect.offices = res
          }),
          // this.apiContract.getProductList(this.query.products).then(res => {
          //   this.dataSelect.products = res.data.content
          // }),
          this.apiEmployee.getEmployeeList(this.query.members).then(res => {
            this.dataSelect.members = res.data
          }),
          this.apiDeal.getDataSalesPipelines(this.query.pipeline).then(res => {
            this.listPipeline = res.data.content
          })
        ])
        break
    }
  }
  checkChangOrRemove(type: string, code: string, item: Record<string, any>, id: number): void {
    this.groupFilter.dataChange[code] = this.groupFilter.dataChange[code] && this.groupFilter.dataChange[code].length > 0 ? this.groupFilter.dataChange[code] : []
    if (type === 'change') {
      let hasItem =
        this.groupFilter.dataChange[code] && this.groupFilter.dataChange[code].length > 0 && item
          ? this.groupFilter.dataChange[code].some((el: Record<string, any>) => el.id == item.id)
          : false
      if (!hasItem && item) {
        this.groupFilter.dataChange[code].push(item)
      }
    } else {
      if (this.groupFilter.dataChange[code] && this.groupFilter.dataChange[code].length > 0) {
        let newList = this.groupFilter.dataChange[code].filter((el: Record<string, any>) => el.id != id)
        this.groupFilter.dataChange[code] = newList
      } else {
        this.groupFilter.dataChange[code] = []
      }
    }
    console.log(this.groupFilter.dataChange)
  }
  checkCode(type: string, code: string | any, id: number): void {
    let item: Record<string, any> = {}
    switch (code) {
      case 'members':
        item = this.dataSelect.members.find((item: Record<string, any>) => item.id == id)
        this.checkChangOrRemove(type, code, item, id)
        break
      case 'offices':
        item = this.dataSelect.offices.find((item: Record<string, any>) => item.id == id)
        this.checkChangOrRemove(type, code, item, id)
        break
      case 'branches':
        item = this.dataSelect.branches.find((item: Record<string, any>) => item.id == id)
        this.checkChangOrRemove(type, code, item, id)
        break
      case 'areas':
        item = this.dataSelect.areas.find((item: Record<string, any>) => item.id == id)
        this.checkChangOrRemove(type, code, item, id)
        break
    }
  }
  handleChange(type: string, code: string | any, ids: Array<number>): void {
    this.$forceUpdate()
    const lastId = ids[ids.length - 1]
    const stateFilter = this.getDealFilter(this.$route.name as string)
    if (stateFilter?.data) {
      this.groupFilter.dataChange = stateFilter?.data
    } else {
      this.groupFilter.dataChange = {}
    }
    if (type === 'clear') {
      this.groupFilter.dataChange[code] = []
    } else {
      this.checkCode(type, code, lastId)
    }
  }

  handleSubmit(type?: string): void {
    console.log(this.groupFilter.dataChange)

    if (type == 'reset') {
      this.groupFilter.dataChange = {
        areas: [],
        branches: [],
        offices: [],
        members: [],
        products: []
      }
      if (this.$route.name == 'dealPipeline' || this.$route.name == 'forecast') {
        this.groupFilter = {
          pipelineId: 1,
          dealStatus: '1'
        }
      } else {
        this.groupFilter = {
          dealStatus: '0'
        }
      }
      this.$emit('reset', this.groupFilter)
    } else {
      let form: any = this.$refs.formData
      form.validate((validate: any) => {
        if (validate) {
          this.$emit('change', this.groupFilter)
        }
      })
    }
  }
  async handleOpen(): Promise<any> {
    this.groupFilter = { ...this.filters }
    const filterStore = this.getDealFilter(this.$route.name as string)
    if (filterStore?.data) {
      this.dataSelect = { ...this.dataSelect, ...filterStore.data }
    }
    console.log(filterStore)

    await this.handleSearch('', '')
  }
  handleClose(): void {
    // this.isOpen = false
    //@ts-ignore
    // this.handleCancel()
  }
  mounted(): void {
    this.apiLocation = getRepository('location', {
      districtId: this.districtId
    })
    this.apiContract = getRepository('contract', {
      districtId: this.districtId
    })
    this.apiEmployee = getRepository('employee', {
      districtId: this.districtId
    })
    this.apiParam = getRepository('param', {
      districtId: this.districtId
    })
    this.apiDeal = getRepository('deal', {
      districtId: this.districtId
    })
  }
}
