










































import PopupMixin from '@/mixins/popup'
import { assign, debounce, trim } from 'lodash'
import { Component, Mixins, Prop, Watch } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import PopupFilterDeal from './PopupDealFilter.vue'

const beDeal = namespace('beDeal')

@Component({ components: { PopupFilterDeal } })
export default class DealFilter extends Mixins(PopupMixin) {
  @Prop({ required: true, type: Object }) filters!: Record<string, any>
  @Prop({ required: true, type: Boolean }) isPipeline!: boolean

  @beDeal.Getter('getDealFilter') getDealFilter!: (dealName: string) => {
    dealName: string
    filters: Record<string, any>
    data: Record<string, any>
  }
  groupFilters: Record<string, any> = {
    search: '',
    sortType: ''
  }
  listSelection = [
    { id: 1, label: 'Pipeline Bảo hiểm Nhân thọ', value: 1 },
    { id: 2, label: 'Pipeline Bảo hiểm Phi nhân thọ', value: 2 },
    { id: 3, label: 'Pipeline Bảo hiểm cho công ty', value: 3 },
    { id: 4, label: 'Thiết lập pipeline', value: 4 }
  ]
  // BRANCH_ID_INCREASE, REVENUE_INCREASE, COMMISSION_INCREASE, BRANCH_ID_DECREASE, REVENUE_DECREASE, COMMISSION_DECREASE

  get sorts(): Array<Record<string, any>> {
    return [
      {
        command: 'DEAL_VALUE',
        label: 'Giá trị',
        divided: false
      },
      {
        command: 'CLOSE_DATE',
        label: 'Ngày chốt',
        divided: false
      },
      {
        command: 'CREATED_AT',
        label: 'Ngày tạo',
        divided: false
      }
    ]
  }
  @Watch('groupFilters.search', { immediate: false }) watchSearch(new_: string): void {
    this.searchDebounce(new_)
  }
  searchDebounce = debounce(this.searchText, 500)
  searchText(value: string): void {
    this.groupFilters.search = trim(value)
    this.$emit('change', this.groupFilters)
    // this.setReportFilter({
    //   report: this.type,
    //   filters: assign(this.groupFilters, {
    //     search: trim(value),
    //     sortType: this.sortType
    //   })
    // })
  }
  handleClick(): void {
    this.setOpenPopup({
      popupName: 'filter-deal',
      isOpen: true
    })
  }
  handleReset(filter: Record<string, any>): void {
    this.groupFilters = filter
  }

  handeChangeFilters(filters: Record<string, any>): void {
    this.groupFilters = { ...this.groupFilters, ...filters }
    this.$emit('change', this.groupFilters)
    this.setOpenPopup({
      popupName: 'filter-deal',
      isOpen: false
    })
  }

  handleCommand(command: string): void {
    if (this.groupFilters.sortType === command) {
      this.groupFilters.sortType = ''
    } else {
      this.groupFilters.sortType = command
    }
    this.$emit('change', this.groupFilters)
  }

  init(): void {
    if (this.$route.name == 'dealPipeline' || this.$route.name == 'forecast') {
      this.groupFilters.dealStatus = '1'
    } else {
      this.groupFilters.dealStatus = '0'
    }
    this.groupFilters = { ...this.groupFilters, ...this.filters }
    const filters = this.getDealFilter(this.$route.name as string)
    if (filters) {
      this.groupFilters = { ...this.groupFilters, ...filters.filters }
    }
    this.$emit('change', this.groupFilters)
  }

  mounted(): void {
    this.init()
  }
}
